/*responsive.scss*/
@import '../_config';

@media only screen and (max-width: 1200px) {
	.graf_top {
		margin-left: 30px;
	}
	.menu span {
		margin-left: 2%;
	}
	.site_name {
		font-size: 35px;
		line-height: 45px;
		padding-top: 150px;
		span {
			font-size: 55px;
		    line-height: 60px;
		}
	}
	.detals {
		left: 50%;
		-webkit-transform: translate(-100%,0);
		transform: translate(-100%,0);
		width: 900px;
	}
	.sale_block,
	.order_block {
		padding: 30px;
	}
}

@media only screen and (max-width: 1100px) {
	.wForm label:not(.error) {
		font-size: 12px;
		line-height: 24px;
	}
	.sale_form .real_price {
		font-size: 25px;
		line-height: 50px;
		span {
			font-size: 35px;
		}
	}
	.sale_form .old_price {
		overflow: hidden;
		span {
			font-size: 16px;
		}
	}
	.wFormDef .wInput, .wFormDef .wInputSpan, .wFormDef .wTextarea, .wFormDef .wSelect {
		padding: 0 10px;
	}
}

@media only screen and (max-width: 1024px) {
	.advantages {
		padding-top: 140px;
	}
	.pageTitle,
	.reviews_title,
	.buy_form_title,
	.buy_title {
		font-size: 30px;
		line-height: 40px;
		&.w_small {
			font-size: 20px;
			line-height: 25px;
		}
	}
	.avd_opus_title {
		font-size: 20px;
		line-height: 25px;
	}
	.adv_unic {
		padding: 0;
	}
	.order_form {
		.wFormRow {
			width: 50%;
			margin-bottom: 10px;
			&:last-child {
				width: 100%;
				margin-top: 10px;
			}
		}
	}
	.compare_list,
	.order_block {
		margin-bottom: 20px;
	}
	.reviews {
		padding: 70px 0;
		min-height: 0;
	}
	.adv_bot_item_title {
		font-size: 25px;
		line-height: 30px;
		margin-bottom: 10px;
	}
	.sale_form .wFormRow {
		padding: 0 5px;
	}
	.wBtn {
		font-size: 12px;
	}
	.logo_block {
		width: 70px;
		.logo {height: 15px;}
	}
	.site_name {
		font-size: 30px;
		line-height: 40px;
		span {
			font-size: 45px;
			line-height: 55px;
		}
	}
	.phone_graf_top {
		padding-top: 10px;
	}
	.phone_top {display: block;}
	.graf_top {margin-left: 0;}
	.graf_top .graf_info span + span {margin-top: 0;}
}

@media only screen and (max-width: 720px) {
	.site_name {
		font-size: 25px;
		line-height: 35px;
		span {
			font-size: 35px;
		    line-height: 40px;
		}
	}
	.detals {
		display: none;
	}
	.top_form {
		width: 100%;
	}
	.adv_khar {
		padding: 0;
	}
	.adv_khar.w_right .adv_khar_img,
	.adv_khar.w_left .adv_khar_img {
		width: 100%;
		float: none;
		margin-bottom: 20px;
		text-align: center;
		img {
			width: 400px;
			display: inline-block;
			vertical-align: top;
		}
	}
	.advantages {
		.pageTitle {
			margin-bottom: 30px;
		}
	}
	.unic_list {
		margin-bottom: 20px;
	}
	.unic_item {
		width: 100%;
		margin: 0 0 10px;
	}
	.compare_list {text-align: center;}
	.compare_item,
	.buy_item {
		width: 49%;
		margin-bottom: 20px;
		& + .buy_item {margin-left: 0;}
		&:nth-child(2) {
			margin-left: 2%;
		}
	}
	.buy {
		padding-top: 0;
		min-height: 0;
	}
	.buy2 {
		padding-top: 0;
		min-height: 0;
		background: #fff;
	}
	.pageTitle,
	.reviews_title,
	.buy_form_title,
	.buy_title {
		text-align: center;
		font-size: 20px;
		line-height: 30px;
		&.w_small {
			font-size: 16px;
			line-height: 20px;
		}
	}
	.adv_bot_item {
		width: 100%;
		& + .adv_bot_item {
			margin-left: 0;
			margin-top: 20px;
		}
	}
	.adv_bot {
		height: auto;
		padding: 30px 10px;
		background: #393c41;
		margin-bottom: 20px;
	}
	.adv_bot_items {
		position: relative;
		-webkit-transform: translate(0,0);
		transform: translate(0,0);
		top: 0;
	}
	.sale_block {
		height: auto;
		margin-bottom: 20px;
	}
	.sale_form {
		.wFormRow {
			width: 100%;
			padding: 0;
		}
		.real_price {
			text-align: center;
		}
		.old_price {
			left: 50%;
			-webkit-transform: translate(-100%,0);
			transform: translate(-100%,0);
		}
	}
	.buy2 {
		background: #fff;
		padding: 0;
		.buy_list2 .buy_name {
			color: #474747;
		}
	}
	.logo_f_block {
		line-height: 35px;
		width: 100%;
		text-align: center;
		img {display: none;}
		span {margin-left: 0;}
	}
	.wezom_block {
		line-height: 35px;
		width: 100%;
		text-align: center;
	}
	.flip-clock-wrapper ul {
		width: 30px;
		height: 45px;
		font-size: 40px;
		line-height: 45px;
	}
	.flip-clock-wrapper ul li a div div.inn {
		font-size: 30px;
		line-height: 45px;
	}
	.flip-clock-divider {
		height: 50px;
		width: 10px;
	}
	.flip-clock-divider .flip-clock-label {
		color: #fff;
	}
	.flip-clock-divider .flip-clock-label,
	.flip-clock-divider.minutes .flip-clock-label,
	.flip-clock-divider.seconds .flip-clock-label {
		right: -55px;
	}
	.timer {width: 270px;}
	.sale_block,
	.order_block,
	.top_form {
		padding: 30px 10px;
	}
	.sale_title {
		margin-bottom: 20px;
	}
	.wHeader {
		height: 70px;
	}
	.logo_block,
	.menu {
		line-height: 70px;
		height: 70px;
	}
	.menu span {
		font-size: 12px;
	}
	.phone_top span {
		font-size: 12px;
	}
	.graf_top .graf_info span {
		font-size: 12px;
	}
	.site_name {
		padding-top: 100px;
		font-size: 18px;
		line-height: 20px;
		span {
			font-size: 25px;
			line-height: 30px;
		}
	}
	.top_form {
		bottom: 0;
	}
	.advantages {
		padding-top: 20px;
	}
	.top_block {
		-webkit-background-size: 260% auto;
		background-size: 260% auto;
	}
}

@media only screen and (max-width: 600px) {
	.phone_graf_top {display: none;}
}

@media only screen and (max-width: 540px) {
	.order_form {
		.wFormRow {
			width: 100%;
			padding: 0;
		}
	}
	.compare_item {
		width: 100%;
		&:nth-child(2) {
			margin-left: 0%;
		}
		& + .compare_item {
			margin-left: 0;
		}
	}
	.buy_list {margin-bottom: 0;}
	.buy_name {
		font-size: 14px;
		line-height: 20px;
	}
	.buy_text {
		font-size: 12px;
		line-height: 18px;
	}
	.buy_svg svg {
		max-height: 100px;
		max-width: 100px;
		height: auto !important;
	}
	.buy_form .wFormRow {
		width: 100%;
		margin-left: 0;
	}
	.menu span {
		margin: 0 2%;
		line-height: 25px;
		width: 46%;
		float: left;
		text-align: center;
	}
	.wHeader,
	.logo_block {
		height: 50px;
		line-height: 50px;
	}
	.wHeader {
		position: relative;
	    background: #0c0c0d;
	}
}

@media only screen and (min-width: 540px) {
	.wHeaderFix {display: none !important;}
}